.home-container {
    width: 100%;
    height: calc(100vh - 220px);
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
}
.home-container h1 {
    font-size: 10rem;
    font-family: NotoSans-Bold;
    text-align: center;
}
.home-arrow-container {
    display: none;
}
@media (max-width: 1000px) {
    .home-container {
        height: calc(100vh - 180px);
        align-items: flex-end;
    }
    .home-container h1 {
        font-size: 8rem;
    }
    .home-arrow-container {
        display: block;
        position: absolute;
        width: 100%;
        padding-top: 30px;
        display: flex;
        justify-content: center;     
    }
    .home-arrow-top {
        transform: rotate(45deg);
        border: solid black;
        border-width: 1px 0 0 1px;
        width: 10px;
        height: 10px;
    }
    .home-arrow {
        height: 30vh;
        width: 1px;
        background-color: black;
        left: -5px;
        top: -1px;
        position: relative;
    }
}
